/* App.css */

.App {
    text-align: center;
    background-color: #0c0c0c; /* Dark background color */
    color: #fff; /* Light text color */
    min-height: 100vh;
  }
  
  .App-header {
    padding: 20px;
    background-color: #007acc; /* Slick blue header background color */
    color: white;
  }
  
  .App-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  .blog-post {
    max-width: 600px;
    margin-bottom: 40px;
    padding: 20px;
    border: 1px solid #333; /* Border color for each blog post */
    border-radius: 8px;
    background-color: #1a1a1a; /* Dark background color for blog post */
  }
  
  .blog-post h2 {
    color: #007acc; /* Slick blue color for blog post titles */
  }
  
  .post-date {
    color: #999; /* Light color for post date */
  }
  
  /* Add more styles as needed */
  